import React from 'react';
import mySvg from './loader.svg'
import styled from 'styled-components';
const Loaderr = styled.div`
  position: absolute;
  left: calc(50% - 100px);
  top: calc(50% - 40px);
  display: inline-block;
  color: white;
  z-index: 10;
`;

const Img = styled.img`
`;

const Loader = ()=> (
  <Loaderr className="blue-txt">
  <Img width="75"  src={mySvg} alt="A Loader" />
  </Loaderr>);

export default Loader;
